import loading from '../components/loading.svg'

export default function Loading() {
  return (
    <main className="text-center">
        <h2 className="text-primary">Loading...</h2>
        <p>Hopefully not for too long :)</p>
        <img className='rotate-center'
        style={{width: '100px'}}
        src={loading}
        alt='Loading...'/>
    </main>
  )
}
